import { Helmet } from "react-helmet-async";
import { useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";

const Wrap = styled.div`
  max-width: 500px;
  min-height: 100vh;
  width: 100%;
  margin: 0 auto;
  background-color: white;
  box-shadow: 0px 0px 5px 5px #e8eaf6;
`;
const Form = styled.form`
  max-width: 500px;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-bottom: 100px;

  h1 {
    font-size: 40px;
    font-weight: 900;

    margin-bottom: 80px;
  }
  input {
    all: unset;
    width: 80%;
    border: 1px solid #ddd;
    margin-top: 14px;
    padding: 10px 15px;
    border-radius: 20px;
  }
`;
const Button = styled.button`
  all: unset;
  width: 80%;
  margin-top: 30px;
  padding: 13px 15px;
  display: flex;
  justify-content: center;
  border-radius: 20px;
  border: 1px solid #ddd;
  background-color: ${(props) => props.$bg};
  cursor: ${(props) => props.$cur};
`;
//
const Errors = styled.div`
  width: 80%;
  padding-top: 5px;
  color: salmon;
`;

export const Login = () => {
  const nav = useNavigate();

  const {
    register,
    handleSubmit,
    formState: { errors, isValid },
  } = useForm();

  const onSubmitHandler = () => {
    console.log("");
    if (isValid === true) {
      nav("/", {
        replace: true,
      });
    }
  };
  console.log();
  console.log(errors?.userpassword?.message);
  return (
    <Wrap>
      <Helmet>
        <title></title>
      </Helmet>
      <Form onSubmit={handleSubmit(onSubmitHandler)}>
        <h1>LOGIN</h1>
        <input
          {...register("username", {
            required: " ",
            minLength: {
              value: 2,
              message: " 2  .",
            },
          })}
          type="text"
          placeholder=" ."
        />
        <Errors>{errors?.username?.message}</Errors>
        <input
          {...register("userpassword", {
            required: " ",
            minLength: {
              value: 8,
              message: " 8  .",
            },
          })}
          type="password"
          placeholder=" ."
        />
        <Errors>{errors?.userpassword?.message}</Errors>

        <Button
          $bg={isValid ? "#7bbdff" : ""}
          $cur={isValid ? "default" : "not-allowed"}
        >
          
        </Button>
      </Form>
    </Wrap>
  );
};
